
  @import url('https://fonts.googleapis.com/css2?family=Borel&family=Open+Sans&family=Preahvihear&family=Rubik:wght@500&display=swap');


/*[Global] */
:root {
  /* color */
  --primary-color: #2b81e4;
  --secondary-color: #eee;
  --white-color: #fff;
  --grey-color: #555;
  --light-grey-color: #777;
  --test-color: yellowgreen;

  /* font size */
  /* Í∏∞Î≥∏ 1rem = 16px */
  --font-large: 1.6rem;
  --font-medium: 1.4rem;
  --font-regular: 1.2rem;
  --font-basic: 1rem;
  --font-micro: 0.8rem;

  /* Font weight */
  /* Í∏∞Î≥∏ 400 */

  --weight-bold: 700;

  --weight-semi-bold: 600;

  --weight-basic: 400;

  /* letterspacing */
  /* Í∏∞Î≥∏ 0.1rem */
  --letterspacing-basic: 0.1rem;
  --letterspacing-test: 2rem;

  /* Size */
  --size-border-basic-radius: 0.5rem;
  --size-border-test-radius: 1rem;

  /* animation */
  --animation_basic-duration: 0.3s;
  --animation_test-duration: 1s;
}

/* COMMON STYLES */

* {
  margin: 0;
  padding: 0;

  font-family: 'Preahvihear', sans-serif;

  /* T */
  list-style-type: none;
  /* T */
  text-decoration: none;
  box-sizing: border-box;

  transition: all 0.2s linear;   
  
  outline: none;
}
/* T */
html {
  font-size: 62.5%;
}

/* T: center ÏÖÄÎ†âÌÑ∞Î•º Îî∞Î°ú ÎßåÎì§Ïñ¥ÏÑú ÌïÑÏöîÌïú Î∂ÄÎ∂ÑÏóê Ï†ÅÏö©Ìï® */
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  background-color: var(--secondary-color);
  /* T: CSS 28 */
  margin: 0.5rem;
  /* T: ÏàòÌèâ,ÏàòÏßÅ, ÌùêÎ¶øÌï®Í≥º ÌôïÏÇ∞ , ÏÉâÏÉÅ */
  box-shadow: .2rem .4rem .2rem rgb(139, 139, 139);
  /*T: CSS 2 : Í±∞Ïä¨Î¶¨Îäî Ïä§ÌÅ¨Î°§Î∞îÎ•º ÏóÜÏï∞ */
