
/*üçÄ [navbar] */

.navbar-icon {
    width: 5.5rem;
    height: 5.5rem;
    background-color: var(--white-color);
    border-radius: 50%;
    cursor: pointer;
    /* CSS 26 . T: navbarÏùò divÎßâÎåÄ 3Í∞úÍ∞Ä rowÎ°ú Í≤πÏ≥êÏûàÎäîÍ±∏ columnÏúºÎ°ú Î∞îÍøà*/
    flex-direction: column;
  }
  
  .open-navbar-icon {
    /* T: fixed. z-index */
    position: fixed;
    top: 6.5rem;
    left: 6.5rem;
    z-index: 200;
  }
  
  /* CSS 26 */
  .navbar-icon .line {
    height: 0.5rem;
    width: 3.5rem;
  }
  .open-navbar-icon .line {
    margin: 0.3rem 0;
  }
  
  .line:nth-child(1){
    background-color: grey;
  }
  .line:nth-child(2){
    background-color: grey;
  }
  .line:nth-child(3){
    background-color: grey;
  }
  
  
  /* üçÄÌÅ¥Î¶≠Ìï¥ÏÑú xÎ™®ÏñëÏúºÎ°ú Î∞îÎÄî Îïå navbar  */
  
  .navbar-wrapper {
    /* display: none; */
    /* T: vw: view width, vh: view height */
    /* ????: Îã§Î•∏Í≥≥ÏùÄ remÏì∞Îã§Í∞Ä, Ïôú Ïó¨Í∏∞ÏóêÏÑúÎäî vw, vhÎ•º ÏçºÏßÄ? */
    width: 100vw;
    height: 100vh;
    background-color: var(--test-color);
    position: fixed;
    left: 0;
    /* CSS 38. js 06 */
    /* bottom: -100%;
        opacity: 0;
    */
    z-index: 300;
    /* opacity: 0.9; */
    /* T: CSS 28 : containerÏôÄ ÌÅ¨Í∏∞ ÎòëÍ∞ôÏù¥ ÎßûÏ∂îÍ∏∞*/
    padding: 3.5rem 5.5rem 3.5rem 3.5rem;
  
    /* CSS 40 */
    /* bottomÏóêÏÑú Ïò¨ÎùºÏò§Îäî ÏÜçÎèÑ, opacity Ï†ÅÏö©ÎêòÎäî ÏÜçÎèÑ */
    transition: bottom 0.5s, opacity 0.2s;
  }
  
  /* üëâr24 */
  .slide-bottom {  
    animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  }  
  
  @keyframes slide-bottom {
    0% {
      transform: translateY(-25%);
    }
    100% {
      transform: translateY(0);
    }
  }

  

  /* CSS 38. js 06 */
  /* navbar-iconÌÅ¥Î¶≠Ìï†Îïå .changeÏ∂îÍ∞Ä */
  .change .navbar-wrapper {
    /* position..ÏúÑÏπò,opacity */
 /*    bottom: 0%;
    opacity: 1;
   */
    /* CSS 40 */
    /* bottomÏóêÏÑú Ïò¨ÎùºÏò§Îäî ÏÜçÎèÑ, opacity Ï†ÅÏö©ÎêòÎäî ÏÜçÎèÑ, ÌôîÎ©¥ÌëúÏãúÎêòÍ∏∞ÍπåÏßÄ delay */
    transition: bottom 0.5s, opacity 0.2s 0.25s;
  }
  
  .navbar {
    width: 100%;
    height: 100%;
  
    /* T: Í≤ÄÏùÄÏÉâgradientÏóêÏÑú opacityÎßå Ï°∞Ï†à */
    /* url(images/navbar-bg.jpg)  */
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),center no-repeat;
    /* CSS 30 ,32*/
    position: relative;
    /*????: T: navbar-linkÍ∞Ä top:-100% Î°ú ÎÇ¥Î†§Í∞ÄÏûàÎäîÏÉÅÌÉúÎùºÏÑú, Î∞ëÏùò hoverÌö®Í≥ºÎ•º Îã§ Í∞ÄÎ†§Î≤ÑÎ¶¨Îäî Î≤ÑÍ∑∏ Í≥†Ï≥êÏ§å */
    overflow-y: hidden;
  }
  
  .close-navbar-icon {
    /* CSS 30 */
    position: absolute;
    top: 3rem;
    left: 3rem;
    z-index: 300;
  }
  /* CSS 32 */
  .close-navbar-icon .line {
    position: absolute;
  }
  /* CSS 32 */
  /* T: 40deg */
  .line-1 {
    transform: rotate(40deg);
  }
  /* CSS 32 */
  .line-2 {
    transform: rotate(-40deg);
  }
  .nav-list {
    /* CSS 34 :T: Ïª®ÌÖåÏù¥ÎÑà ÌÅ¨Í∏∞ÎßåÌÅºÏùò height. */
    height: 100%;
    /* T: */
    display: flex;
  }
  
  .nav-link {
    /* CSS 34 */
    width: 100%;
    /*css 34 : Í∏ÄÏûêÌÅ¨Í∏∞ÎßåÌÅºÏùò height */
    height: 100%;
    font-size: 3rem;
    font-weight: 700;
    color: var(--white-color);
    text-transform: uppercase;
  
    opacity: 0.8;
  
    /* CSS 38. js 06 */
    position: relative;
    /* top: -100%; */
    /* CSS 36 */
    /* transition: all 0.3s; */
  }
  /* CSS 38. js 06 */
  .change .nav-link {
    top: 0;
  }
  
  /* CSS 36 */
  .nav-link:hover {
    opacity: 1;
    color: purple;
  }
  
